.header {
	display: flex;
	justify-content: space-between;
	padding: 10px;
	.logo {
		font-weight: 600; }
	.headerActions {
		display: flex;
		.notification {
			display: flex;
			align-self: center;
			position: relative;
			cursor: pointer;
			svg {
				cursor: pointer;
				margin-right: 10px; } }
		.user_container {
			button {
				display: flex;
				background-color: white;
				color: black;
				border-radius: 5px;
				border: 1px solid #dadada;
				&::after {
					margin-top: 10px; }
				&:hover, &:focus {
					border-radius: 5px;
					background-color: #d6d6d6 !important;
					box-shadow: none;
					color: black;
					border-color: #dadada; } }
			.userIcon {
				margin-right: 10px; }
			&:hover {
				background-color: #d6d6d6; } }
		.logout {
			display: flex;
			align-self: center;
			margin-left: 10px;
			cursor: pointer; } } }
.notification_count {
	position: absolute;
	right: 5px;
	top: 0px;
	height: 20px;
	width: 20px;
	background: white;
	border-radius: 50%;
	font-size: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
	p {
		margin: 0; } }
.has_notification {
	border: 2px solid #ff7765; }
.no_notification {
	border: 2px solid black; }
.logout_yes {
	width: 50%;
	border: 2px solid #ff7765;
	background: #ff7765;
	&:hover {
		background: darken(#ff7765, 10);
		border-color: darken(#ff7765, 10); } }
.logout_no {
	width: 50%; }
