//==============================================
// ============ COMMON =========================
html, body {
	font-family: 'VisbyCF-Regular', Arial, Helvetica, sans-serif;
	letter-spacing: 0.6px;
	background-color: rgb(247, 247, 247); }

.no_padding {
	padding: 0; }
.dropdown-menu {
	padding: 0;
	.dropdown-item {
		padding: .5rem 1.5rem; }
	.dropdown-item:not(:last-child) {
		border-bottom: 1px solid #dadada; } }

.spinner {
	animation: spin infinite 1s linear; }

@keyframes spin {
	0% {
		transform: rotate(0deg); }
	100% {
		transform: rotate(360deg); } }

.dropdown-item {
	&:active {
		background-color: #f8f9fa;
		color: black; } }
.react-date-picker__calendar {
	z-index: 9999 !important; }

.react-date-picker, .react-datetime-picker, .react-timerange-picker, .react-time-picker {
	display: block;
	width: 100%;
	padding: .375rem .75rem;
	font-size: 1rem;
	line-height: 1.5;
	color: #495057;
	background-color: #fff;
	background-clip: padding-box;
	border: 1px solid #ced4da;
	border-radius: .25rem;
	transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
	.react-date-picker__wrapper, .react-datetime-picker__wrapper, .react-timerange-picker__wrapper, .react-time-picker__wrapper {
		border: 0; } }

ul.pagination {
	.page-link {
		color: black; } }

.page-item.active {
	.page-link {
		background-color: black !important;
		color: white;
		border-color: black; } }

.dropdown-item.active {
	background-color: $bittersweet; }

.tableHeight {
	max-height: 100vh; }

a.nav-link.active {
	background-color: $bittersweet; }
.table-responsive {
	max-height: 77vh;
	border: 1px solid rgba(0, 0, 0, 0.13); }

.form_container {
	background-color: #e9ecef;
	padding: 15px 10px; }
.clickable_td {
	color: $bittersweet;
	font-weight: bold;
	position: relative; }
.clickable_td_emp {
	cursor: pointer;
	&:hover {
		text-decoration: underline; } }

// th, td
