	align-items: center;
	justify-content: center;
	border-width: 2px;
	border-radius: 1rem;
	border-style: dashed;
	border-color: #cbd5e1;
	background-color: #ffffff;
	&.drag-active {
		background-color: darken(#f8fafc,0% );
 } }		// background-color: red
.upload-button {
	cursor: pointer;
	padding: 0.25rem;
	font-size: 1rem;
	border: none;
	font-family: 'Oswald', sans-serif;
	background-color: transparent;
	&:hover {
		text-decoration-line: underline; } }
#drag-file-element {
	position: absolute;
	width: 100%;
