// Custom Dropdown
.custom_dropdown {
	position: relative;
	button {
		color: #fff;
		background-color: #343a40;
		border-color: #343a40;
		display: inline-block;
		font-weight: 400;
		text-align: center;
		white-space: nowrap;
		vertical-align: middle;
		-webkit-user-select: none;
		-moz-user-select: none;
		-ms-user-select: none;
		user-select: none;
		border: 1px solid transparent;
		padding: 0.375rem 0.75rem;
		font-size: 1rem;
		line-height: 1.5;
		border-radius: 0.25rem;
		margin-right: 1rem; } }
.custom_menu {
	position: absolute;
	list-style-type: none;
	margin: 5px 0;
	padding: 0.2rem 0.5rem;
	background: white;
	border: 1px solid #d2d2d2;
	z-index: 999;
	& > li {
		margin: 0;
		background-color: white;
		& > button {
			width: 100%;
			height: 100%;
			text-align: left;
			background: none;
			color: inherit;
			border: none;
			padding: 5px;
			margin: 0;
			font: inherit;
			cursor: pointer;
			display: inline-flex;
			align-items: center; } } }

.file_name {
	text-overflow: ellipsis;
	width: 400px;
	overflow: hidden;
	white-space: nowrap; }

.required_document_input {
